import React, { Component } from 'react';
import { Button } from 'react-bootstrap';
// import  from 'react-bootstrap/Button';
import { PieChart, Pie, Tooltip, ResponsiveContainer, Cell } from 'recharts';

class Event extends Component {
  state = {
    events: [],
    showDetails: false
  };

  handleShowDetails = () => {
    if (this.state.showDetails === false) {
      this.setState({ showDetails: true });
    } else {
      this.setState({ showDetails: false });
    }
  };
  getRSVPData = () => {
    let eventAttendents = this.state.events;
    for (let i = 0; i < eventAttendents.length; i++) {
      if (
        eventAttendents[i] === this.props.event.yes_rsvp_count &&
        eventAttendents[i] === this.props.event.rsvp_limit
      ) {
        console.log('I AM WORKING');
      }
    }

    let rsvpCount = this.props.event.yes_rsvp_count;
    let rsvpLimit = this.props.event.rsvp_limit - rsvpCount;

    return [
      { name: 'Reservations', value: rsvpCount },
      { name: 'Open Spots', value: rsvpLimit }
    ];
  };

  render() {
    const { event } = this.props;
    let colors = ['#e63070', '#ff8a00'];
    return (
      <div className="Event">
        <div className="detailsOverview">
          <span className="eventName">Event: {event.name}</span> <br></br>
          <span>Date: {event.local_date}</span>
          <span> Time: {event.local_time}</span>
          <Button
            className="btn details-btn"
            onClick={() => this.handleShowDetails()}>
            Show More
          </Button>
        </div>

        {this.state.showDetails && (
          <div className="expandedDetails">
            <p
              className="description"
              dangerouslySetInnerHTML={{ __html: event.description }}
            />
            <p className="rsvpCount">
              Number Attending: {event.yes_rsvp_count}
            </p>
            <p className="rsvpLimit">RSVP Limit: {event.rsvp_limit}</p>

            <ResponsiveContainer height={100}>
              <PieChart
                width={20}
                height={20}
                margin={{ top: 20, right: 30, left: 0, bottom: 0 }}>
                <Pie
                  data={this.getRSVPData()}
                  cx={50}
                  cy={50}
                  outerRadius={80}
                  fill="#82ca9d"
                  dataKey="value"
                  nameKey="name">
                  {this.getRSVPData().map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={colors[index]} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
            <Button
              className="btn details-btn"
              onClick={() => this.handleShowDetails()}>
              Show Less
            </Button>
          </div>
        )}
      </div>
    );
  }
}

export default Event;
